
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
}
button {
  padding: 10px;
  margin: 10px;
  background-color: #4dc3ff;
  border-style: none;
  color: #000000;
  font-weight: bold;
  cursor: pointer;
}
button:disabled {
  background: #dddddd;
  color: #000000;
  cursor: no-drop;
}

.question-list[data-v-4d73680a] {
  list-style-type: none;
  padding: 0px;
  /* width: 45%; */
  margin: 2.5%;
  float: left;
}
.left[data-v-4d73680a] {
  width: 60%;
}
.right[data-v-4d73680a] {
  width: 30%;
}
.container[data-v-4d73680a] {
  margin: 10px;
}

.right {
  float: right;
  padding: 10px;
  margin: 10px;
}
.left {
  float: left;
  /* width: 80%; */
}
.clear {
  clear: both;
  text-align: left;
}
.icon {
  cursor: pointer;
}
.description {
  text-align: justify;
  margin: 0 auto;
}
.inProgress {
  font-weight: bold;
  background-color: #4dc3ff;
}
.list-element {
  cursor: pointer;
  text-align: justify;
}
.answerNote {
  font-style: italic;
}
.warning {
  background-color: #ffff66;
  color: black;
}
.bold {
  font-weight: bold;
}
li {
  type: none;
}
li div {
  padding: 5px;
}
.notAnswer:nth-child(odd) {
  background: #e6e6e6;
}
.notAnswer:nth-child(even) {
  background: #f2f2f2;
}
.isAnswer:nth-child(odd) {
  background: #99ff99;
}
.isAnswer:nth-child(even) {
  background: #80ff80;
}
textarea {
  padding: 10px;
  margin: 10px;
  width: 80%;
  height: 100px;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-leave-active {
  /* position: absolute; */
}
.fade-move {
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.fade-enter {
  opacity: 0;
}
.fade-leave-to {
  opacity: 0;
}





















































